*{
  margin: 0;
  padding:0;
}
body {
  margin:0;
  font-family:Arial;
  background-color: #0a192f;

}

.topnav {
overflow: hidden;
background-color: #0a192f;
width:100%;

}
.topnav img{
  height:35px;
  width:35px;
  cursor:pointer;

}
.topnav img:hover{
  background-color: #52a39e;
}
.topnav-right {
  float:right;
}

.topnav a {
float: left;
display: block;
color: #f2f2f2;
text-align: center;  
text-decoration: none;
padding-top:5px;
padding-right: 15px;
padding-left: 15px;
font-size: 30px;
margin-top:10px;
cursor:pointer;
}

.topnav a:hover{
color: #52a39e;

}

#profile{
  height:600px;
  background-color: #0a192f;
}
#profileImage{
  display: block;
  padding-top:50px;
  margin-left: auto;
  margin-right: auto;


}
#profile h1{
  color:white;
  text-align: center;
  padding-top: 50px;
  font-size: 40px;
}
#profile h3{
  color:#52a39e;
  text-align: center;
  padding-top: 50px;
  font-size: 20px;
}
#profile button {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  border:none;
  width:100px;
  height:100px;
  border-radius:100%;
  background-color: #52a39e;
  color:white;
  font-size: 50px;
}
#profile button a{
  color:white;
}
button:hover {
  background-color:lightgreen;
  cursor: pointer;

}

#introduction{
  background-color:white;
  overflow: hidden;
  margin-bottom:20px;
}
#introduction h2{
  margin-top: 10px;
  font-size: 50px;
  color:#0a192f;

}

#acmPhoto{
  margin: 20px 20px;
  border-radius: 10px;
  width:40%;
  float:left;
}
#description{
  float: right;
  height:500px;
  width:40%;
  margin-right:50px;
  margin-top:10px;
  color: grey;
}
#description p{
  margin-top:10px;
  font-size:1.2em;
  
  
}
#description button{
  font-size:25px;
  background-color: #52a39e;
  border:none;
  padding:20px;
  border-radius: 5px;
  margin-top: 20px;
  margin-left:25%;
  
}
#description button:hover{
  background-color:lightgreen;
  
  
}
#description button a{

  color:white;
  text-decoration: none;
  
}
#introduction{
  float:left;
  background-color:#e6ffff ;
}


#skills {
  
  width:100%;
  overflow: hidden;
  
}
#skillsHeader{
  color:#52a39e;
  list-style: none;
  font-size: 35px;
}
#skills ul{
  width:50%;
  height:320px;
  padding:25px 30px;
  margin-top: 35px;
  margin-right: 20px;
  margin-left: 20px;
  border-radius: 20px;
  float:left;
  background-color: #0a192f;
  color:white;
  font-size: 25px;
}
#skills h1 a{
  text-decoration: none;
  color:#52a39e;
}
#skills img{
  width:30%;
}
#projects{
  background-color: whitesmoke;
  padding-top: 20px;
  padding-bottom: 20px;
  text-align: center;
  color:#52a39e;
  font-size: 25px;
}
#projects a{
  text-decoration: none;
}
.project1{
  background-color: white;
  width:90%;
  margin-top: 1%;
  margin-bottom: 2%;
  margin-left: 5%;
  height:500px;
  border-radius: 20px;
  overflow: hidden;

}
.projectInfo1{
  background-color: #0a192f;
  width:50%;
  float:left;
  text-align: center;
  height:500px;
}
.projectInfo1 a{
  color:white;
}
.projectInfo1 a:hover{
  color:#52a39e;
}
.projectDescription1{
  background-color: white;
  margin-top: 5%;
  color:black;
  margin-right:5%;
  width:40%;
  float:right;
  text-align: center;
  height:500px;
}
.projectName1{
  padding-top:5%;
}


.project2{
  background-color: #52a39e;
  width:90%;
  margin-top: 1%;
  margin-bottom: 2%;
  margin-left: 5%;
  height:500px;
  border-radius: 20px;
  overflow: hidden;

}
.projectInfo2{
  background-color: white;
  width:50%;
  float:left;
  text-align: center;
  height:500px;
}
.projectInfo2 a{
  color:#0a192f;
}
.projectInfo2 a:hover{
  color:#52a39e;
}
.projectDescription2{
  margin-top: 5%;
  color:white;
  margin-right:5%;
  width:40%;
  float:right;
  text-align: center;
  height:500px;
}
.projectName1{
  padding-top:5%;
}



.projectImage{
  margin-top:5%;
  width:80%;
  border-radius:10px;
}

#moreProjects{
background-color:#e6ffff ;;
  padding:20px 40px;
  overflow-x: scroll;
}

#moreProjects a{
  text-decoration: none;
}
.mProject{
  background-color: #52a39e;
  width:250px;
  height:300px;
  text-align: center;
  border-radius:10px;
  margin-right:10px;
  margin-top:10px;
  float:left;
  color:white;
}
.mProject:hover{
  background-color: #0a192f;
}
.mProject h4{
  font-size: 30px;
  padding-top:5px;
}
.mProject p{
  font-size: 20px;
  padding-top:5px;
}
.mProject img{
  padding-top:20px;
}

footer{
  background-color: #0a192f;
  color:white;
  text-align:center;
  padding-top:20px;
  font-size:30px;
  height:200px;
}

#contactme a {
  
  margin: 10px;
 
}


@media only screen and (max-width: 900px) {
  .topnav a {
     
      font-size: 18px;
      
  }
  .topnav img{
      width:30px;
      height:30px;
      margin-top:10px;
      margin-right:10px;
  }
  #acmPhoto{
      margin: 20px 20px;
      border-radius: 10px;
      width:40%;
      float:left;
  }
  #description p{
      font-size: 1em;
  }
  #skills ul{
      width:40%;
  }
  #skills img{
      margin-top:50px;
      width:40%;
  }
  .projectInfo1 a{
      font-size:25px;
  }
  .projectInfo2 a{
      font-size:25px;
  }
  .mProject{
      width:200px;
      height:400px;
  }


}